import React, { useEffect } from "react";
import Stack from "@mui/material/Stack";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Container from "@mui/material/Container";
import {
  CustomDialog,
  CustomInput,
  ImageInput,
  ModalNotification,
  TopNav,
} from "../../components";
import { useNavigate } from "react-router-dom";
import makeStyles from "@mui/styles/makeStyles";
import { connect } from "react-redux";
import {
  getPendidikan,
  getAgama,
  getPekerjaan,
  getJenisIdentitas,
  getStatusNikah,
  register,
} from "../../redux/actions";
import { isFormValid, isSelectedFieldValid } from "./validation";

import {
  Checkbox,
  Dialog,
  FormControlLabel,
  FormGroup,
  FormLabel,
  Grid,
  Paper,
  Radio,
  RadioGroup,
  Typography,
} from "@mui/material";

const useStyles = makeStyles(() => ({
  root: {
    padding: "16px 0",
  },
  langContainer: {
    padding: "16px 0",
  },
  dialogImg: {
    height: "250px",
    width: "auto",
  },
}));
const RegisterPage = (props) => {
  const {
    actGetPendidikan,
    actGetAgama,
    actGetPekerjaan,
    actGetJenisID,
    actGetStatusNikah,
    stateAgama,
    statePendidikan,
    statePekerjaan,
    stateStatusNikah,
    stateJenisID,
    stateError,
    actRegister,
  } = props;

  const formInitial = {
    // filename: Date.now(),
    // image: "",
    jenisId: "",
    jenisIdnomor: "",
    namaLengkap: "",
    namaPanggilan: "",
    tempatLahir: "",
    tglLahir: "",
    jenisKelamin: "",
    alamat: "",
    rt: "",
    rw: "",
    kelurahan: "",
    kecamatan: "",
    kotaDati: "",
    kodePos: "",
    propinsi: "",
    noTelpon: "",
    agama: "",
    nilaiYakin: "",
    textNilaiYakin: "",
    statusNikah: "",
    statusWarganegara: "",
    infoIbu: "",
    infoAyah: "",
    infoIstriSuami: "",
    noPenjamin: "",
    namaWaliDarurat: "",
    telponWaliDarurat: "",
    fileUpload: "",
    filePhoto: "",
    // fligFlag: true,
    // inputWaktu: "",
    noTelpon2: "",
    alamatDomisili: "",
    statusPendidikan: "",
    penjamin: "",
    bahasa: "",
    infoKerja: "",
    penjaminSubjenis: 0,
    // id_pasien_ps_id: null,
  };

  const invalidInitial = {
    jenisId: "",
    jenisIdnomor: "",
    namaLengkap: "",
    namaPanggilan: "",
    tempatLahir: "",
    tglLahir: "",
    jenisKelamin: "",
    alamat: "",
    rt: "",
    rw: "",
    kelurahan: "",
    kecamatan: "",
    kotaDati: "",
    kodePos: "",
    propinsi: "",
    noTelpon: "",
    agama: "",
    nilaiYakin: "",
    textNilaiYakin: "",
    statusNikah: "",
    statusWarganegara: "",
    infoIbu: "",
    infoAyah: "",
    infoIstriSuami: "",
    noPenjamin: "",
    namaWaliDarurat: "",
    telponWaliDarurat: "",
    fileUpload: "",
    filePhoto: "",
    // fligFlag: true,
    // inputWaktu: "",
    noTelpon2: "",
    alamatDomisili: "",
    statusPendidikan: "",
    penjamin: "",
    bahasa: "",
    infoKerja: "",
    penjaminSubjenis: 0,
    // id_pasien_ps_id: null,
  };

  const classes = useStyles();
  const [openFail, setOpenFail] = React.useState(false);
  const [sameAddress, setHandleSameAddress] = React.useState(false);
  const [errorForm, setErrorForm] = React.useState("");
  const [succForm, setSuccForm] = React.useState(false);

  const [form, setForm] = React.useState(formInitial);
  const [formInvalid, setInvalid] = React.useState(invalidInitial);
  const [bahasa, setBahasa] = React.useState("");
  const [openBahasa, setOpenBahasa] = React.useState(true);

  const navigate = useNavigate();
  React.useEffect(() => {
    if (stateError) {
      navigate("/exception-handling");
    }
  }, [stateError]);

  React.useEffect(() => {
    actGetPekerjaan();
    actGetAgama();
    actGetPendidikan();
    actGetJenisID();
    actGetStatusNikah();
  }, []);

  const changeForm = (elem) => {
    let { name, id, value } = elem.target;
    let { error } = isSelectedFieldValid(name ? name : id, value);
    setInvalid({
      ...formInvalid,
      [name ? name : id]: error ? "*" + error.message : "",
    });
    return setForm({
      ...form,
      [name ? name : id]: value,
    });
    // return;
  };
  const handleChangeImg = (elem) => {
    return setForm({
      ...form,
      filePhoto: elem.target.value,
      fileUpload: elem.target.value,
    });
  };
  const handleSubmit = () => {
    let isValid = isFormValid(form);
    if (!isValid.error) {
      return actRegister(form, {
        success: () => {
          setForm(formInitial);
          setSuccForm(true);
        },
        failed: (msg) => 
          setErrorForm(msg)
      });
    } else {
      setErrorForm(isValid.error.details[0].message);
    }
  };
  const handleBahasa = (val) => {
    setOpenBahasa(false);
    return setBahasa(val);
  };

  const handleSameAddress = () => {
    setHandleSameAddress(!sameAddress);
    setForm({ ...form, alamat: form.alamatDomisili });
  };
  return (
    <>
      <CustomDialog
        setOpen={setSuccForm}
        type={"success"}
        title={"Berhasil Terdaftar!."}
        open={succForm}
      />
      <CustomDialog
        setOpen={setErrorForm}
        type={"error"}
        title={errorForm}
        open={errorForm ? true : false}
      />
      <Dialog open={openBahasa}>
        <Container className={classes.langContainer}>
          <Grid container spacing={2}>
            <Grid xs={12} item container justifyContent={"center"}>
              <img
                src="./assets/language.png"
                alt="language"
                className={classes.dialogImg}
              />
            </Grid>
            <Grid xs={12} item>
              <Typography align="center" variant="body2">
                {/* <a href="https://storyset.com/people"> */}
                People illustrations by Storyset
                {/* </a> */}
              </Typography>
              <Typography align="center" variant="body1">
                Bahasa yang digunakan / Language
              </Typography>
            </Grid>
            <Grid xs={12} item>
              <Button
                variant="contained"
                onClick={() => handleBahasa("indonesia")}
              >
                Bahasa Indonesia
              </Button>
            </Grid>
            <Grid xs={12} item>
              <Button
                variant="outlined"
                onClick={() => handleBahasa("english")}
              >
                English
              </Button>
            </Grid>
          </Grid>
        </Container>
      </Dialog>
      {/* <Dialog open={errorForm ? true : false} onClose={() => setErrorForm("")}>
        <Paper >
          <Typography align="center">{errorForm}</Typography>
        </Paper>
      </Dialog> */}
      <Container className={classes.root}>
        <Stack spacing={2}>
          <TopNav
            type="back"
            title="Daftar Pasien Baru"
            click={() => navigate(-1)}
          />
          <FormControl fullWidth required>
            <InputLabel id="jenisId">
              {bahasa === "indonesia" ? "Jenis Identitas" : "Identity Type"}
            </InputLabel>
            <Select
              labelId="jenisId"
              id="jenisId"
              name="jenisId"
              value={form.jenisId}
              onChange={changeForm}
            >
              {stateJenisID &&
                stateJenisID.map((item) => {
                  return (
                    <MenuItem
                      key={"jenisID" + item.ref_jki_id}
                      value={item.ref_jki_id}
                    >
                      {item.ref_jki_ket}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <TextField
            id="jenisIdnomor"
            name="jenisIdnomor"
            label={bahasa === "indonesia" ? "No Identitas" : "Identity Number"}
            required
            error={formInvalid.jenisIdnomor ? true : false}
            value={form.jenisIdnomor}
            onChange={changeForm}
            helperText={formInvalid.jenisIdnomor}
          />
          <TextField
            id="namaLengkap"
            name="namaLengkap"
            label={bahasa === "indonesia" ? "Nama Lengkap" : "Fullname"}
            required
            value={form.namaLengkap}
            error={formInvalid.namaLengkap ? true : false}
            onChange={changeForm}
            helperText={formInvalid.namaLengkap}
          />
          <TextField
            id="namaPanggilan"
            name="namaPanggilan"
            label={bahasa === "indonesia" ? "Nama Panggilan" : "Nickname"}
            required
            value={form.namaPanggilan}
            onChange={changeForm}
            error={formInvalid.namaPanggilan ? true : false}
            helperText={formInvalid.namaPanggilan}
          />
          <TextField
            id="tempatLahir"
            name="tempatLahir"
            label={bahasa === "indonesia" ? "Tempat Lahir" : "Birthplace"}
            // label="Tempat Lahir - Birthplace"
            required
            value={form.tempatLahir}
            onChange={changeForm}
            error={formInvalid.tempatLahir ? true : false}
            helperText={formInvalid.tempatLahir}
          />
          <TextField
            id="tglLahir"
            name="tglLahir"
            label={bahasa === "indonesia" ? "Tanggal Lahir" : "Birthdate"}
            // label="Tanggal Lahir - Birthdate"
            required
            InputLabelProps={{
              shrink: true,
            }}
            type={"date"}
            value={form.tglLahir}
            onChange={changeForm}
            error={formInvalid.tglLahir ? true : false}
            helperText={formInvalid.tglLahir}
          />
          <FormControl required>
            <FormLabel id="jenisKelamin">
              {bahasa === "indonesia" ? "Jenis Kelamin" : "Gender"}
            </FormLabel>
            {/* <FormLabel id="jenisIdentitas">Jenis Kelamin - Gender</FormLabel> */}
            <RadioGroup
              row
              aria-labelledby="jeniKelamin"
              id="jenisKelamin"
              name="jenisKelamin"
              value={form.jenisKelamin}
              onChange={changeForm}
            >
              <FormControlLabel
                value="1"
                control={<Radio />}
                label={bahasa === "indonesia" ? "Pria" : "Male"}
                // label="Pria - Male"
              />
              <FormControlLabel
                value="2"
                control={<Radio />}
                label={bahasa === "indonesia" ? "Perempuan" : "Female"}

                // label="Perempuan - Female"
              />
            </RadioGroup>
          </FormControl>
          <TextField
            id="alamatDomisili"
            name="alamatDomisili"
            // label="Alamat Domisili - Current Address"
            label={
              bahasa === "indonesia" ? "Alamat Domisili" : "Current Address"
            }
            required
            error={formInvalid.alamatDomisili ? true : false}
            value={form.alamatDomisili}
            onChange={changeForm}
            helperText={formInvalid.alamatDomisili}
          />
          <Stack direction="row" spacing={2}>
            <TextField
              fullWidth
              id="rt"
              name="rt"
              label="RT"
              required
              value={form.rt}
              onChange={changeForm}
              helperText={formInvalid.rw}
              error={formInvalid.rt ? true : false}

              // value={type}
              // onChange={changeType}
            />
            <TextField
              fullWidth
              id="rw"
              name="rw"
              label="RW"
              required
              value={form.rw}
              onChange={changeForm}
              helperText={formInvalid.rw}
              error={formInvalid.rw ? true : false}
            />
          </Stack>
          <TextField
            fullWidth
            id="kelurahan"
            name="kelurahan"
            // label="Kelurahan - Subdistrict"
            label={bahasa === "indonesia" ? "Kelurahan" : "Subdistrict"}
            required
            value={form.kelurahan}
            onChange={changeForm}
            helperText={formInvalid.kelurahan}
            error={formInvalid.kelurahan ? true : false}
          />
          <TextField
            fullWidth
            id="kecamatan"
            // label="Kecamatan - Country"
            label={bahasa === "indonesia" ? "Kecamatan" : "Country"}
            required
            value={form.kecamatan}
            onChange={changeForm}
            helperText={formInvalid.kecamatan}
            error={formInvalid.kecamatan ? true : false}
          />
          <TextField
            fullWidth
            id="propinsi"
            name="propinsi"
            // label="Provinsi - Province"
            label={bahasa === "indonesia" ? "Provinsi" : "Province"}
            required
            value={form.propinsi}
            onChange={changeForm}
            helperText={formInvalid.propinsi}
            error={formInvalid.propinsi ? true : false}
          />
          <TextField
            fullWidth
            id="kotaDati"
            name="kotaDati"
            // label="Kotamadya / Dati II - Regency"
            label={bahasa === "indonesia" ? "Kotamadya / Dati II" : "Regency"}
            required
            value={form.kotaDati}
            onChange={changeForm}
            helperText={formInvalid.kotaDati}
            error={formInvalid.kotaDati ? true : false}
          />
          <TextField
            fullWidth
            id="kodePos"
            name="kodePos"
            // label="Kode Pos - Post Code"
            label={bahasa === "indonesia" ? "Kode Pos" : "Post Code"}
            required
            value={form.kodePos}
            onChange={changeForm}
            helperText={formInvalid.kodePos}
            error={formInvalid.kodePos ? true : false}
          />
          <div>
            <TextField
              fullWidth
              id="alamat"
              name="alamat"
              // label="Alamat Sesuai Identitas - Address ID"
              label={
                bahasa === "indonesia"
                  ? "Alamat Sesuai Identitas"
                  : "Address ID"
              }
              required
              disabled={sameAddress}
              value={sameAddress ? form.alamatDomisili : form.alamat}
              onChange={changeForm}
              helperText={formInvalid.alamat}
              error={formInvalid.alamat ? true : false}
            />
            <FormGroup>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={sameAddress}
                    onChange={handleSameAddress}
                  />
                }
                label={
                  bahasa === "indonesia"
                    ? "Sama dengan alamat domisili"
                    : "Same as current address"
                }
              />
            </FormGroup>
          </div>

          <Stack direction="row" spacing={2}>
            <CustomInput
              id="noTelpon"
              name="noTelpon"
              label={bahasa === "indonesia" ? "No Telp 1" : "Phone Number 1"}
              fullWidth
              required
              value={form.noTelpon}
              onChange={changeForm}
              helperText={formInvalid.noTelpon}
              className={classes.inputPhone}
              inputType="phone-number"
              error={formInvalid.noTelpon ? true : false}
            />
            {/* <TextField
              fullWidth
              id="noTelpon"
              name="noTelpon"
              // label="No Telp - Phone Number 1"
              label={bahasa === "indonesia" ? "No Telp 1" : "Phone Number 1"}
              required
              value={form.noTelpon}
              onChange={changeForm}
              helperText={formInvalid.noTelpon}
              // value={type}
              // onChange={changeType}
            /> */}
            <CustomInput
              id="noTelpon2"
              name="noTelpon2"
              label={bahasa === "indonesia" ? "No Telp 2" : "Phone Number 2"}
              fullWidth
              required
              className={classes.inputPhone}
              inputType="phone-number"
              value={form.noTelpon2}
              onChange={changeForm}
              helperText={formInvalid.noTelpon2}
            />
            {/* <TextField
              fullWidth
              id="noTelpon2"
              name="noTelpon2"
              fullWidth
              label={bahasa === "indonesia" ? "No Telp 2" : "Phone Number 2"}
              // label="No Telp - Phone Number 2"
              required
              value={form.noTelpon2}
              onChange={changeForm}
              helperText={formInvalid.noTelpon2}
            /> */}
          </Stack>
          <FormControl fullWidth required>
            {/* <InputLabel id="agama">Agama - Religion</InputLabel> */}
            <InputLabel id="agama">
              {bahasa === "indonesia" ? "Agama" : "Religion"}
            </InputLabel>
            <Select
              labelId="agama"
              id="agama"
              name="agama"
              value={form.agama}
              onChange={changeForm}

              //   value={age}
              //   label="Age"
              //   onChange={handleChange}
            >
              {stateAgama &&
                stateAgama.map((item) => {
                  return (
                    <MenuItem
                      key={"agama" + item.ref_agama_id}
                      value={item.ref_agama_id}
                    >
                      {item.ref_agama_ket}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <Stack spacing={1}>
            <FormControl required>
              {/* <FormLabel id="keyakinan">
              Keyakinan dan Nilai-Nilai Pribadi - Personal Believes and Values
            </FormLabel> */}
              <FormLabel id="keyakinan">
                {bahasa === "indonesia"
                  ? "Keyakinan dan Nilai-Nilai Pribadi"
                  : "Personal Believes and Values"}
              </FormLabel>
              <RadioGroup
                row
                aria-labelledby="keyakinan"
                name="nilaiYakin"
                id="nilaiYakin"
                value={form.nilaiYakin}
                onChange={changeForm}
              >
                <FormControlLabel
                  value={0}
                  control={<Radio />}
                  label={bahasa === "indonesia" ? "Tidak" : "No"}
                />
                <FormControlLabel
                  value={1}
                  control={<Radio />}
                  // label="Ya (Tidak mau imunisasi/Tidak mau transfusi/Tidak mau pulang sabtu/Vegetarian)"
                  label={
                    bahasa === "indonesia"
                      ? "Ya (Tidak mau imunisasi/Tidak mau transfusi/Tidak mau pulang sabtu/Vegetarian"
                      : "Yes (Won't immunization/Won't Transfusion/Won't go home on Saturday/Vegetarian)"
                  }
                />
              </RadioGroup>
            </FormControl>
            <TextField
              fullWidth
              id="textNilaiYakin"
              name="textNilaiYakin"
              disabled={form.nilaiYakin === "1" ? false : true}
              label={
                bahasa === "indonesia" ? "Jika ya, sebutkan" : "If yes, mention"
              }
              required
              value={form.textNilaiYakin}
              onChange={changeForm}
              helperText={formInvalid.textNilaiYakin}
            />
          </Stack>
          <FormControl fullWidth required>
            {/* <InputLabel id="agama">
              Status Pernikahan - Marital Status
            </InputLabel> */}
            <InputLabel id="agama">
              {bahasa === "indonesia" ? "Status Pernikahan" : "Marital Status"}
            </InputLabel>
            <Select
              labelId="statusNikah"
              id="statusNikah"
              name="statusNikah"
              value={form.statusNikah}
              onChange={changeForm}
            >
              {stateStatusNikah &&
                stateStatusNikah.map((item) => {
                  return (
                    <MenuItem
                      key={"jenisID" + item.stat_id}
                      value={item.stat_id}
                    >
                      {item.stat_ket}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <FormControl fullWidth required>
            {/* <InputLabel id="pendidikan">Pendidikan - Education</InputLabel> */}
            <InputLabel id="pendidikan">
              {bahasa === "indonesia" ? "Pendidikan" : "Education"}
            </InputLabel>
            <Select
              labelId="statusPendidikan"
              id="statusPendidikan"
              name="statusPendidikan"
              value={form.statusPendidikan}
              onChange={changeForm}
            >
              {statePendidikan &&
                statePendidikan.map((item) => {
                  return (
                    <MenuItem
                      key={"pnddkn" + item.pend_id}
                      value={item.pend_id}
                    >
                      {item.pend_nama}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <FormControl required>
            {/* <FormLabel id="kewarganegaraan">
              Kewarganegaraan - Nationality
            </FormLabel> */}
            <FormLabel id="kewarganegaraan">
              {bahasa === "indonesia" ? "Kewarganegaraan" : "Nationality"}
            </FormLabel>
            <RadioGroup
              row
              aria-labelledby="statusWarganegara"
              name="statusWarganegara"
              value={form.statusWarganegara}
              onChange={changeForm}
            >
              <FormControlLabel
                value="1"
                control={<Radio />}
                // label="WNI - Citizen"
                label={bahasa === "indonesia" ? "WNI" : "Citizen"}
              />
              <FormControlLabel
                value="2"
                control={<Radio />}
                // Foreigner
                label={bahasa === "indonesia" ? "WNA" : "Foreigner"}
                // label="WNA - Foreigner"
              />
            </RadioGroup>
          </FormControl>
          <TextField
            fullWidth
            id="bahasa"
            name="bahasa"
            label={bahasa === "indonesia" ? "Bahasa" : "Language"}
            // label="Bahasa - Language"
            required
            value={form.bahasa}
            onChange={changeForm}
            helperText={formInvalid.bahasa}
            error={formInvalid.bahasa ? true : false}
          />
          <FormControl fullWidth required>
            <InputLabel id="pekerjaan">
              {bahasa === "indonesia" ? "Pekerjaan" : "Occupation"}
            </InputLabel>
            {/* <InputLabel id="pekerjaan">Pekerjaan - Occupation</InputLabel> */}
            <Select
              labelId="infoKerja"
              id="infoKerja"
              name="infoKerja"
              value={form.infoKerja}
              onChange={changeForm}
            >
              {statePekerjaan &&
                statePekerjaan.map((item) => {
                  return (
                    <MenuItem
                      key={"pkrjn" + item.ref_pkrjn_id}
                      value={item.ref_pkrjn_id}
                    >
                      {item.ref_pkrjn_nama}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <TextField
            fullWidth
            id="infoAyah"
            name="infoAyah"
            // label="Nama Bapak - Father's Name"
            label={bahasa === "indonesia" ? "Nama Bapak" : "Father's Name"}
            required
            value={form.infoAyah}
            onChange={changeForm}
            helperText={formInvalid.infoAyah}
          />
          <TextField
            fullWidth
            id="infoIstriSuami"
            name="infoIstriSuami"
            // label="Nama Suami / Istri - Husband's / Wife's Name"
            label={
              bahasa === "indonesia"
                ? "Nama Suami / Istri"
                : "Husband's / Wife's Name"
            }
            required
            value={form.infoIstriSuami}
            onChange={changeForm}
            helperText={formInvalid.infoIstriSuami}
          />
          <Stack spacing={1}>
            <FormControl fullWidth required>
              <InputLabel id="penjamin">
                {bahasa === "indonesia" ? "Penjamin" : "Insurance"}
              </InputLabel>
              {/* <InputLabel id="penjamin">Penjamin - Insurance</InputLabel> */}
              <Select
                labelId="penjamin"
                id="penjamin"
                name="penjamin"
                value={form.penjamin}
                onChange={changeForm}
              >
                <MenuItem value={1}>Umum - Public</MenuItem>
                <MenuItem value={2}>BPJS - BPJS</MenuItem>
                <MenuItem value={3}>Perusahaan - Company</MenuItem>
                <MenuItem value={4}>Asuransi - Insurance</MenuItem>
              </Select>
            </FormControl>
            <TextField
              fullWidth
              id="noPenjamin"
              name="noPenjamin"
              label={bahasa === "indonesia" ? "No Penjamin" : "Insurance No"}
              // label="No Penjamin - Insurance No"
              required
              value={form.noPenjamin}
              onChange={changeForm}
              helperText={formInvalid.noPenjamin}
            />
          </Stack>
          <Stack direction={"row"} spacing={2}>
            <TextField
              fullWidth
              id="namaWaliDarurat"
              name="namaWaliDarurat"
              label={bahasa === "indonesia" ? "Nama Wali" : "Guarantor's Name"}
              // label="Nama Wali - Guarantor's Name"
              required
              value={form.namaWaliDarurat}
              onChange={changeForm}
              helperText={formInvalid.namaWaliDarurat}
              error={formInvalid.namaWaliDarurat ? true : false}
            />
            <CustomInput
              id="telponWaliDarurat"
              name="telponWaliDarurat"
              fullWidth
              label={
                bahasa === "indonesia"
                  ? "Telp Penanggung"
                  : "Guarantor's Phone Number"
              }
              required
              className={classes.inputPhone}
              inputType="phone-number"
              value={form.telponWaliDarurat}
              onChange={changeForm}
              helperText={formInvalid.telponWaliDarurat}
              error={formInvalid.telponWaliDarurat ? true : false}
            />
            {/* <TextField
              fullWidth
              id="telponWaliDarurat"
              label={
                bahasa === "indonesia"
                  ? "Telp Penanggung"
                  : "Guarantor's Phone Number"
              }
              // label="Telp Penanggung - Guarantor's Phone Number"
              value={form.telponWaliDarurat}
              onChange={changeForm}
              helperText={formInvalid.telponWaliDarurat}
            /> */}
          </Stack>
          <ImageInput
            is_required={true}
            is_readonly={false}
            // field_name={"Foto Identitas - KTP - ID Photo"}
            field_name={
              bahasa === "indonesia" ? "Foto Identitas - KTP" : "ID Photo"
            }
            default_value={form.filePhoto}
            value={form.filePhoto}
            form_child_id={""}
            handleChange={handleChangeImg}
            is_error={false}
          />
          <Button
            variant="outlined"
            onClick={() => setOpenBahasa(true)}
            // onClick={() => navigate("/")}
          >
            Ganti Bahasa / Change Language
          </Button>
          <Button
            variant="contained"
            onClick={handleSubmit}
            // onClick={() => navigate("/")}
          >
            {bahasa === "indonesia" ? "Daftar" : "Submit"}
          </Button>
        </Stack>
      </Container>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    stateAgama: state.master.agama,
    statePendidikan: state.master.pendidikan,
    statePekerjaan: state.master.pekerjaan,
    stateJenisID: state.master.jenisIdentitas,
    stateStatusNikah: state.master.statusNikah,
    stateError: state.app.error,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actGetPendidikan: (action) => dispatch(getPendidikan(action)),
    actGetAgama: (action) => dispatch(getAgama(action)),
    actGetPekerjaan: (action) => dispatch(getPekerjaan(action)),
    actGetJenisID: (action) => dispatch(getJenisIdentitas(action)),
    actGetStatusNikah: (action) => dispatch(getStatusNikah(action)),
    actRegister: (body, action) => dispatch(register(body, action)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RegisterPage);
